@import url(https://fonts.googleapis.com/css2?family=Antic&family=Montserrat:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  /* text-align: center; */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

html, body {
    width: 100%;
    height: 100%;
}
body{
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    font-weight: 100 !important;
}
.mainPage{
    background: url(/shani-perpinial-24-1-21/static/media/4.c2f8f755.jpg);
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    background-repeat: no-repeat;
    height: 100%;
    width: 100%;
    min-width: 250px;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    position: absolute;
    z-index: -1;
}
header{
    position: relative;
    margin: 10px 2px;
}
header span{
    padding: 2px;
    /* float: right; */
    right: 3%;
    font-size: 16px;
    cursor: pointer;
}
.show { display: block; }
.hide { display: none; }
#searchButton{
    width: 200px;
    margin: auto;
    top:10%;
    opacity:0.5;
    margin-top: 20px;
}
.dailyWeather{
    position: relative;
    width: 300px;
    height: 150px;
    left: 16%;
    top: 60px;
}
.addFavorite{
    font-size: 24px !important;
    position: absolute;
    left: -40px;
    top: 7px;
    cursor: pointer;
}
.inFavorites{
    color:red;
}
.clear {
    clear: both;
}
#cityName{
    text-align: left !important;
    font-size: 32px;
    position: absolute;
}

#currentDailyTemp{
    font-size: 80px;
    position: absolute;
    padding-top: 30px;
}

.dailyWeather .icon{
    font-size: 40px;
    position: absolute;
    left: 150px;
    top: 72px;
}

.weeklyWeather{
    position: relative;
    text-align: center;
    width: 80%;
    margin-left: 10%;
    margin-top: 350px;   
}

ul{
    flex-direction: row;
    /* align-items: flex-start; */
    display: flex;
    
}
.weatherItems{
    background-color: rgba(240, 248, 255, 0.6);
    flex-grow: 1;
    height:100%;
    align-self: center;
    border-radius: 8%;
    font-size: 18px;
    justify-content:space-around;
    width: 150px;
}

.weatherItems +.weatherItems{
    margin-left: 12%;
}
li{
    list-style-type: none;
    opacity: 1;
}
li span{
    clear: both;
    display: block;
}

@media (max-width: 860px) {
    .dailyWeather{
        width: 100%;
        top: 150px;
    }

    .weeklyWeather{
        margin-top:200px;
        width: 100%;
        margin-left: 0;
    }

    ul{
        flex-direction: column; 
        width: 100% !important;
        padding-left: 0px !important; 
        padding-right: 0px !important; 
    }
    ul span{
        display: inline-block;
    }
    ul .forecastHeadline{
        padding-left: 10px;
        display: block;
        clear: both;
    }
    ul .temperature {
        display: block;
    }
    li{
        padding-top: 5px;
        padding-bottom: 5px;
    }
    .weatherItems{
        width: 100%;
        border-radius: 0;
        height:100%;
        justify-content:space-around;
        flex-grow: 1;
        margin-bottom: 5px;
        background-color: none;
    }
    .weatherItems +.weatherItems{
        margin-left: 0px !important;

    }
    .iconAndTemp{
        float: right;
        margin-right: 40px;
        margin-top: -40px;
        position: relative;
    }
    .weeklyIcon{
        padding-left: 5px;
    }
    #cityName{
        text-align: left !important;
    }
}   /* media query */

  #errorWrapper {
    position: absolute;
    width: 100%;
    bottom: 10px;
  }
  #errorToast {
    position: relative;
    margin: 0 auto;
    background-color: rgba(112, 112, 112, 0.7);
    width: 350px;
    height: 20px;
    padding: 10px;
    text-align: center;
    border-radius: 10px;
    color: white;
    display: none;
  }
  #errorToast.show {
    display: block !important;
  }



.externalLinks{ 
    margin-bottom: 2px;
    left: 20px;
    position: absolute;
    bottom: 20px;
    font-size: 25px;
     cursor:pointer;
    margin: 0 auto;
     
}
#github{
    margin-left: 5px;
    color: black;
}
#linkedin{
    color: black;

}

@media (max-width: 860px) {
.externalLinks{
    bottom: 12px;

}

}

#favoritesContainer{
    width: 70%;
    margin: auto;
}

#favoritesContainer .weatherItems{
}

#favoritesContainer .weatherItems li span{
    display: block;
    clear: both;
}

#searchBar{
    position: relative;
    width: 200px;
    margin: auto;
    z-index: 100;
  }
  #searchWrapper {
    position: relative;
  }
  #searchResult {
    position: absolute;
    cursor: pointer;
    width: 220px;
    z-index: 200;
  }
  #searchinput {
    display: block;
    width: 100%;
    height: calc(1.5em + .75rem + 2px);
    padding: 0 10px;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;

}
  #searchResult div {
    text-align: left;
    width: 100%px;
    padding: 0;
    text-align: left;
    max-height: 280px;
    overflow: hidden;
    overflow-y: auto;
    background-color: #ffffff;
    border: 1px solid;
    border-top: none;
  }
  #searchResult div:hover {
    background-color: #0069ff;
    color: #FAFBFC;
  }
  
